.mobile-menu {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: var(--primary-bg-color);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: transform .4s ease-in-out, opacity .4s ease-in-out, visibility .4s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  transform: translateX(100%);
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transition: transform .4s ease-in-out, opacity .4s ease-in-out, visibility .5s ease-in-out;
  transform: translateX(0%);
}

